.pageBtn {
  height: 45px;
  width: 45px;
  margin: 0 2px;
  padding: 10px;
  color: #508edf;
  font-size: 16px;
  text-align: center;
  background: #fff;
  border: 2px solid lightgray;
  border-radius: 5px;
  text-align: center;
  cursor: pointer;
}

.pageBtn:hover {
  border: 2px solid #508edf;
  background: rgba(150, 165, 212, 0.24);
}

.active {
  color: #508edf;
  background: #fff;
}
.disabled {
  color: #999;
  pointer-events: none;
  box-shadow: none;
}
